.stockInfo {
    flex: 1;
    margin-bottom: 10px;
    
  }
  
  .stockInfo:not(:last-child) {
    margin-right: 10px;
  }
  
 
  
  .stockInfo--red {
    border-color: red;
  }
  
  .stockInfo__cases--green {
    color: lightgreen !important;
  }
  
  .stockInfo__stock {
    color: #000;
    font-weight: bold;
    font-family: 'Times New Roman', Times, serif;
    font-size: 60px;
    margin-bottom: 0.5rem;
  }
  
  .stockInfo__type {
    color: #6c757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;
  }
  .stockInfo__market
  {
    font-weight: bold!important;
    color: #000!important;

  }
  .stockInfo__price
  {
      
    display: flex;
    justify-content: space-between;
  }
  .prev__close
  {
    display: flex;

    flex-direction: column;
  }
  .prev_price
  {
    color: #000!important;
    font-weight: bold!important;
  }


  .stockInfo__c
  {
      font-size: 30px;
      
  }

  .stockInfo__type > hr{
      border-top: 5px solid black;
  }

  .left
  {
      
      display: flex;
      flex-direction: column;
  }

  .right
  {
      min-width: 250px;
      display: flex;
      flex-direction: column;
  }

.diffPer
{
    font-size: 30px;
    color: rgb(2, 128, 90);
}

.text-secondary
{
    color: #5d5d5f;
}
.range
{
    display: flex;
    justify-content: space-between;
}

.dotted
{
    margin-top: 20px;
    margin-bottom: 20px;
    border-top: 2px dashed grey;
}

.overview__info
{
    flex-direction: column;
    margin-top: 10px;
}
.row
{
    display: flex;
    justify-content: space-between ;
    /* border-bottom: 1px solid gray; */
    padding-bottom: 15px;
}